document.addEventListener('DOMContentLoaded', function() {
    crearGaleria();
})

function crearGaleria() {
    const galeria = document.querySelector('.galeria-imagenes')

    for(let i = 1; i <= 12; i++) {
        const imagen = document.createElement('IMG');
        imagen.src = `build/img/thumb/${i}.webp`;
        imagen.classList.add('thumb-img')
        imagen.dataset.imagenId = i; //Estableciendo un id para cada imagen

        //Enlazar mostrarImagen a las imÃ¡genes:
        imagen.onclick = mostrarImagen;

        const lista = document.createElement('LI');
        lista.appendChild(imagen);

        galeria.appendChild(lista);
        console.log('lista creada');
    }
}

function mostrarImagen(e) {
    const id = parseInt(e.target.dataset.imagenId)

    const imagenGrande = document.createElement('IMG');
    imagenGrande.src = `build/img/grande/${id}.webp`;
    console.log(imagenGrande);

    const overlay = document.createElement('DIV')
    overlay.appendChild(imagenGrande);

    overlay.classList.add('overlay');

    //Boton para cerrar la imagen
    const cerrarImagen = document.createElement('P');
    cerrarImagen.textContent = ''
    cerrarImagen.classList ='btn-cerrar';

    //Cuando se presiona se cierra la imagen
    overlay.onclick = function() {
        overlay.remove();
        body.classList.remove('fijar-body');
    }
    overlay.appendChild(cerrarImagen)

    //Agregar una clase al body que evita poder dar scroll
    const body = document.querySelector('body');
    body.appendChild(overlay);
    body.classList.add('fijar-body');

    buttonNext = document.createElement('P');
    buttonNext.textContent = '';
    buttonNext.classList.add('flechaDerecha');
    buttonNext.classList.add('flechas');

    //Boton Izquierdo
    buttonPrev = document.createElement('P');
    buttonPrev.textContent = '';
    buttonPrev.classList.add('flechaIzq');
    buttonPrev.classList.add('flechas');

    //Las agrego al "overlay"
    overlay.appendChild(buttonNext)
    overlay.appendChild(buttonPrev)
    
    buttonNext.onclick = siguienteImagen;
    buttonPrev.onclick = anteriorImagen;
}
